.App {
  position: relative; /* Make the App container position relative */
  min-height: 100vh; /* Set a minimum height to cover the full viewport */
}

.content {
  position: relative; /* Make the content container position relative */
  z-index: 1; /* Set a higher z-index value to ensure it appears above the particles */
}

#tsparticles {
  position: fixed; /* Make the Particle component position fixed */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
